import {Component, OnInit} from '@angular/core';
import {DataService} from "../../Services/DataService";
import 'chartjs-adapter-moment';
import {DataGetters} from "../../Services/DataGetters";
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-bms-scheme',
  templateUrl: './bms-scheme.component.html',
  styleUrls: ['./bms-scheme.component.css'],
})
export class BmsSchemeComponent implements OnInit{
  pantographData: any = {};
  cellsData: any = {};
  batteriesData: any = {};
  bmsLast: any = {};
  powerInputData: any = {};

  selectedDataName = "";
  showPopup = false;

  resultObject: any = {};
  resultLastData: any = {};
  chartData: any = {};
  constructor(private dataService: DataService, public dataGetters: DataGetters, private titleService: Title) {
    setInterval(() => {
      this.batteriesData = this.dataGetters.getBattariesData();
      this.cellsData = this.dataGetters.getCellsData();
      this.bmsLast = this.dataGetters.getBMSLast();
      this.pantographData = this.dataGetters.getPantografData();
      this.powerInputData = this.dataGetters.getPowerInputData();
    }, 1000)
  }

  ngOnInit() {
    this.setTitle('S11123 - BMS')
  }

  public setTitle(newTitle: string) {
    this.titleService.setTitle(newTitle);
  }

  getBMSData(dataName: string, hours: number = 1) {
    this.resultObject = {};
    this.resultLastData = {};
    this.chartData = {};
    this.selectedDataName = dataName;
    this.showPopup = true;
    this.dataService.getData(this.selectedDataName, hours).then(data => {
      this.resultObject = data.reduce((acc, { _field, _value, _time }) => {
        if (!acc[_field]) {
          acc[_field] = [];
          acc[_field + 'Time'] = [];
        }
        acc[_field].push(_value.toFixed(2));
        acc[_field + 'Time'].push(new Date(_time).getTime());
        return acc;
      }, {});
      this.resultLastData = Object.keys(this.resultObject).reduce((acc: any, key) => {
        if(!key.includes('Time')){
          acc[key] = this.resultObject[key][this.resultObject[key].length - 1];
        }
        return acc;
      }, {});
      this.resultLastData.name = data[0]._measurement;

      for(let item in this.resultObject) {

        if(!item.includes('Time')){
          let tempArray:any[] = [];

          this.resultObject[item].map((elem, index) => {
            tempArray.push([this.resultObject[`${item}Time`][index], +elem]);
          })
          this.chartData[item] = tempArray;
        }
      }
    })
  }

  protected readonly Object = Object;
}
