  selectedDay: number = 0;
  year: number;
  isDaySchedule = false;
  isYearSchedule = false;
  schedule: any = [];
  resultSchedule: any = [];
  allYearSchedule: any = [];

  responseData: any = [];

  showPopup = false;

  newSchedule: any = [];
  selectedTime: string | any = '';
  selectedMode: string = 'WAIT'

  private storageKey = 'dataStorage'

  constructor(private http: HttpClient, private titleService: Title) {
    const date = new Date();
    this.month = date.getMonth();
    this.selectedDay = date.getDate();
    this.selectedMonth = date.toLocaleString('default', { month: 'long' });
    this.year = date.getFullYear();
    this.updateDays();
    this.initializeData();
    this.schedule = this.getData();
    this.getSchedule()
  }
  ngOnInit() {
    this.setTitle('S11123 - Расписание')
  }

  public setTitle(newTitle: string) {
    this.titleService.setTitle(newTitle);
  }

  initializeData(): void {
    if (!this.getData()) {
      this.setData(Schedule);
    }
  }

  getData(): any {
    const data = localStorage.getItem(this.storageKey);
    return data ? JSON.parse(data) : null;
  }

  setData(data: any): void {
    localStorage.setItem(this.storageKey, JSON.stringify(data));
  }

  updateData(newData: any): void {
    let data = this.getData();
    const index = data.findIndex(item => item.id === newData.id);
    if (index !== -1) {
      data[index] = newData;
      this.setData(data);
    }
  }

  addData(newData: any): void {
    let data = this.getData();
    data.push(newData);
    this.setData(data);
  }

