import {Injectable} from "@angular/core";
import {DataService} from "./DataService";

@Injectable({
  providedIn: 'root'
})
export class DataGetters {
  batteriesData: any = {};
  cellsData: any = [];
  pantographData: any = {};
  powerInputData: any = {};
  bmsLastData: any = {};
  ocppData: any = {};
  lines: any = [];

  isCellsDataGot = false;
  isCellsDataError = false;

  isBMSDataGot = false;
  isBMSDataError = false;

  isBastardisesGot = false;
  isBatteriesDataError = false;

  isPantographGot = false;
  isPantographDataError = false;

  isPowerInputDataGot = false;
  isPowerInputDataError = false;

  isColsDataGot = false;
  isColsDataError = false;

  isLinesDataGot = false;
  isLinesDataError = false;

  constructor(private dataService: DataService) {}

  getPantografData() {
    this.dataService.getData('pantograf').then((responseData) => {
      this.pantographData = responseData.reduce((acc, {_field, _value}) => {
        if(!acc[_field]) {
          acc[_field] = {}
        }
        acc[_field] = _value.toFixed(0);
        return acc
      }, {})
      this.isPantographGot = !this.isPantographGot
    }).catch(() => {
      this.isPantographGot = false;
      this.isPantographDataError = true;
    })

    return {"pantografData": this.pantographData, "isPantografDataGetted": this.isPantographGot, "isPantografDataError": this.isPantographDataError };
  }

  getCellsData() {
    this.dataService.getData('cells').then(data => {
      this.cellsData = data.reduce((acc, { _field, _value, _measurement }) => {
        if(!acc[_measurement]) {
          acc[_measurement] = {};
        }

        acc[_measurement][_field]= _value;
        return acc
      }, {})
      this.isCellsDataGot = !this.isCellsDataGot;
    }).catch((error) => {
      this.isCellsDataError = true;
      this.isCellsDataGot = false;
    })

    for(let item in this.cellsData) {
      this.cellsData[item]['kW'] = ((this.cellsData[item].set_current * this.cellsData[item].set_voltage) / 1000).toFixed(0)
    }

    let newObj = {};

    for (let cellKey in this.cellsData) {
      let cellNumber = cellKey.split('_')[1].toLowerCase();

      newObj[cellNumber + '_inv_kw'] = this.cellsData[cellKey].kW+'kW';
      newObj[cellNumber + '_inv_current'] = this.cellsData[cellKey].set_current.toFixed(2) + 'A';
    }

    for (let key in newObj) {
      let element = document.getElementById(key);
      if (element) {
        element.textContent = newObj[key];
      }
    }

    return {"isCellsDataGetted": this.isCellsDataGot, "isCellsDataError": this.isCellsDataError}
  }

  getBattariesData() {
    this.dataService.getData('lastBattaries').then(data => {
      this.batteriesData = data.reduce((acc, { _field, _value, _measurement }) => {
        if(!acc[_measurement]) {
          acc[_measurement] = {};
        }

        acc[_measurement][_field]= _value.toFixed(1);
        return acc
      }, {})
      this.isBastardisesGot = !this.isBastardisesGot
    }).catch((error) => {
      this.isBastardisesGot = false;
      this.isBatteriesDataError = true;
    })

    return {"battariesData": this.batteriesData, "isBattariesDataGetted": this.isBastardisesGot, "isBattariesDataError": this.isBatteriesDataError};
  }

  getBMSLast() {
    this.dataService.getData('lastBms').then(responseData => {
      this.bmsLastData = responseData.reduce((acc, {_field, _value}) => {
        if(!acc[_field]) {
          acc[_field] = {}
        }

        acc[_field] = _value.toFixed(0);
        return acc
      }, {})

      this.isBMSDataGot = !this.isBMSDataGot
    }).catch((error) => {
      this.isBMSDataGot = false;
      this.isBMSDataError = true;
    })

    return {"bmsLastData": this.bmsLastData, "isBMSDataGetted": this.isBMSDataGot, "isBMSDataError": this.isBMSDataError}
  }

  getPowerInputData() {
    this.dataService.getData('powerInput').then(responseData => {
      this.powerInputData = responseData.reduce((acc, {_field, _value}) => {
        if(!acc[_field]) {
          acc[_field] = {}
        }

        acc[_field] = _value.toFixed(0);
        return acc
      }, {})

      this.isPowerInputDataGot = !this.isPowerInputDataGot
    }).catch((error) => {
      this.isPowerInputDataGot = false;
      this.isPowerInputDataError = true;
    })

    return {"powerInputData": this.powerInputData, "isPowerInputDataGot": this.isPowerInputDataGot, "isPowerInputDataError": this.isPowerInputDataError}
  }

  getOcppColData() {
    this.dataService.getData('ocpp').then((responseData) => {

      this.ocppData = responseData.reduce((acc, {_field, _value, _measurement}) => {
        acc[`${_measurement}_${_field}`] = _value.toFixed(0);
        return acc
      }, {});

      this.isColsDataGot = !this.isColsDataGot;
    }).catch(() => {
      this.isColsDataError = true;
    })

    let newObj = {};

    for (let key in this.ocppData) {
      let splitKey = key.split('_');
      let newKey = splitKey[0] + '_' + splitKey[2];
      if (newObj[newKey] === undefined) {
        newObj[newKey] = this.ocppData[key];
      } else {
        newObj[newKey] = newObj[newKey] + '/' + this.ocppData[key];
      }
    }

    for(let key in newObj) {
      if (key.includes('current')) {
        newObj[key] += 'A';
      } else if (key.includes('voltage')) {
        newObj[key] += 'V';
      } else if (key.includes('power')) {
        newObj[key] += 'kW';
      }
    }

    for (let key in newObj) {
      let element = document.getElementById(key);
      if (element) {
        element.textContent = newObj[key];
      }
    }

    return {ocppData: newObj, isColsDataGot: this.isColsDataGot, isColsDataError: this.isColsDataError};
  }

  updateVisibility(lines) {
    for (let key in lines) {
      let element = document.getElementById(key);

      if (element) {
        element.style.opacity = lines[key] ? "1" : "0";
      }
    }
  }

  getLines() {
    this.dataService.getData('cellsSession').then((responseData) => {
      this.lines = responseData.reduce((acc, {_value, _measurement}) => {
        let num2 = parseInt(_measurement.slice(6));
        for (let i = 700405; i <= 700412; i++) {
          let newKey = i + "_to_inv" + num2;
          acc[newKey] = (_value !== 255 && 700412 - i === _value);
        }
        return acc;
      }, {});
      this.isLinesDataGot = !this.isLinesDataGot;
      this.updateVisibility(this.lines);
    }).catch(() => {
      this.isLinesDataError = true;
    });

    return {lines: this.lines, isLinesDataGot: this.isLinesDataGot, isLinesDataError: this.isLinesDataError};
  }

}
