import {Component, OnInit} from '@angular/core';
import {DataGetters} from "../../Services/DataGetters";
import {DataService} from "../../Services/DataService";
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-ocpp',
  templateUrl: './ocpp.component.html',
  styleUrls: ['./ocpp.component.css']
})
export class OcppComponent implements OnInit{
  linesData: any = {};
  lines: any = [];
  ocppData: any = {}

  resultObject: any = {};
  resultLastData: any = {};
  chartData: any = {};

  selectedDataName = "";
  showPopup = false;

  coolArray: string[] = [];

  constructor(private dataGetters: DataGetters, private dataService: DataService, private titleService: Title) {
    setInterval(() => {
      this.ocppData = this.dataGetters.getOcppColData();
      this.linesData = this.dataGetters.getLines();
    }, 1000)
  }

  ngOnInit() {
    for(let i = 21; i <= 41; i++) {
      this.coolArray.push(`U${i}`)
    }

    this.setTitle('S11123 - OCPP')
  }

  public setTitle(newTitle: string) {
    this.titleService.setTitle(newTitle);
  }

  getOcppData(dataName: string, hours: number = 1) {
    this.resultObject = {};
    this.resultLastData = {};
    this.chartData = {};
    this.selectedDataName = dataName;
    this.showPopup = true;
    this.dataService.getData(this.selectedDataName, hours).then(data => {
      this.resultObject = data.reduce((acc, { _field, _value, _time }) => {
        if (!acc[_field]) {
          acc[_field] = [];
          acc[_field + 'Time'] = [];
        }
        acc[_field].push(_value.toFixed(2));
        acc[_field + 'Time'].push(new Date(_time).getTime());
        return acc;
      }, {});

      this.resultLastData = Object.keys(this.resultObject).reduce((acc: any, key) => {
        if(!key.includes('Time')){
          acc[key] = this.resultObject[key][this.resultObject[key].length - 1];
        }
        return acc;
      }, {});
      this.resultLastData.name = data[0]._measurement;

      for(let item in this.resultObject) {

        if(!item.includes('Time')){
          let tempArray:any[] = [];

          this.resultObject[item].map((elem, index) => {
            tempArray.push([this.resultObject[`${item}Time`][index], +elem]);
          })
          this.chartData[item] = tempArray;
        }
      }
    })
  }

  protected readonly Object = Object;
}

